    </el-dialog>
    <!--   USDT -->
    <el-dialog
        title="USDT"
        :visible.sync="usdtBorrowDialogVisible"
        width="36em"
        center>
      <el-tabs v-model="dialogActiveName">
        <el-tab-pane label="" name="first">
          <el-form label-position="top" label-width="80px" :model="supplyUsdt">
            <el-form-item label="">
              <el-input v-model="borrowUsdt.borrowAmount" placeholder="" clearable>
                <el-button slot="prepend" @click="getBorrowBalance(`USDT`)">80%</el-button>
